.main {
  padding: 40px;
  max-width: 720px;
  width: 720px;
  background: #1c1c21;
  border: 1px solid rgba(76, 76, 255, 0.2);
  box-shadow: 0px 24px 32px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.04), 0px 4px 8px rgba(0, 0, 0, 0.04),
    0px 0px 1px rgba(0, 0, 0, 0.04);
  border-radius: 40px;
  position: relative;
  box-sizing: border-box;
  cursor: default;
  z-index: 7;
}

.closeButton {
  padding: 0;
  border: 0;
  background-color: transparent;
  position: absolute;
  top: 60px;
  right: 40px;
  cursor: pointer;
}
.closeButton:hover {
  opacity: 0.6;
}
