@import url(../src/vendors/fonts/fonts.css);

body {
  display: flex;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", "Font for password", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #131316;
}

#root {
  width: 100vw;
  display: flex;
  justify-content: center;
}

input[type="password"] {
  font-family: "Font for password";
}

.input__container .input_size_default {
  width: 480px;
}

.button__button .button_button_size_medium {
  width: 100px;
}
